/* _colors.scss */
$green: rgba(137, 168, 148, 1);
$light-blue: rgba(210, 225, 228, 1);
$dark-brown: rgba(49, 35, 30, 1);
$brown: rgba(104, 78, 50, 1);
$beige: rgba(206, 200, 181, 1);
$white: rgba(255, 255, 255, 1);
$black: rgba(0,0,0,1);

header {
  background: $light-blue;
  box-shadow: 1px 3px 5px $dark-brown;
}
body {
  background: $light-blue;
}
.quote {
  background: $white;
  color: $green;
}
